import React, { Component } from 'react';
import './App.scss';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';
import guessCorpus from './assets/words-source-curated5.txt';
import dictionary from './assets/words-dict-curated5.txt'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Snowfall from 'react-snowfall'
import Help from './Help';
import Stats from './Stats';
import Constants from './Constants'

class App extends Component {
  constructor() {
    super()
    this.myContainer = React.createRef()
    this.keyboard = React.createRef()
  }
  state = {
    guessCorpus: [],
    dictionary: [],
    guess: false,
    wordLength: 5,
    rowIndex: 0,
    colIndex: 0,
    inputs: [],
    over: false,
    won: false,
    lost: false,
    showHelp: false,
    showStats: false,
    themeMode: '',
  }

  async loadWords() {
    fetch(guessCorpus)
      .then(r => r.text())
      .then(text => {
        const words = text.split('\n').filter(word => word.length === this.state.wordLength);
        console.log(`Read ${words.length} words`);

        this.setState({
          guessCorpus: words,
          guess: this.guessWord(words),
          inputs: this.formInputs()
        }, () => {
          fetch(dictionary)
            .then(r => r.text())
            .then(text => {
              const words = text.split('\n').filter(word => word.length === this.state.wordLength);
              this.setState({
                dictionary: [...words, ...this.state.guessCorpus]
              })
            })
        })
      });

  }


  async initThemeMode() {
    const systemMode = window.matchMedia( "(prefers-color-scheme: dark)").matches ? 'dark' : 'light';
    this.setState({
      themeMode: systemMode
    })
  }

  async applyThemeMode(mode) {
    const htmlDom = document.querySelector('html');
    htmlDom.classList.remove('light', 'dark')
    htmlDom.classList.add(mode)
  }

  async toggleThemeMode() {
    this.setState({
      themeMode: this.state.themeMode === 'dark' ? 'light' : 'dark'
    }, () => {
      this.applyThemeMode(this.state.themeMode);
    })
  }

  componentDidMount() {
    this.loadWords()
    if (!window.localStorage.getItem('help-shown')) {
      this.setState({
        showHelp: true,
      }, () => {
        window.localStorage.setItem('help-shown', true)
      })
    }
    this.initThemeMode()
  }

  guessWord(words) {
    return words[Math.floor(Math.random() * words.length)];
  }

  formInputs() {
    const inputs = []
    for (let i = 0; i <= this.state.wordLength; i++) {
      inputs.push(new Array(this.state.wordLength).fill(''))
    }
    return inputs;
  }

  restart() {
    const cells = document.getElementsByClassName('cell');
    for (let cell of cells) {
      cell.classList.remove('correct')
      cell.classList.remove('partial')
      cell.classList.remove('miss')
    }
    this.keyboard.removeButtonTheme('Q W E R T Y U I O P A S D F G H J K L M N B V C X Z', 'partial miss correct')
    this.setState({
      guess: this.guessWord(this.state.guessCorpus),
      inputs: this.formInputs(),
      rowIndex: 0,
      colIndex: 0,
      over: false,
      won: false,
      lost: false
    })
  }

  async checkWord() {

    const word = this.state.inputs[this.state.rowIndex].join('');
    console.log(this.state.rowIndex, word, '---')
    if (word.length < this.state.wordLength) {
      toast.warn('Word is too short!', {
        position: 'top-center',
        autoClose: 3000
      })
    } else if (this.state.dictionary.find(cand => cand === word)) {
      console.log('Word found in the dictionary')

      console.log('check and highilgiht')
      const cells = document.getElementsByClassName('cell');
      const row = this.state.rowIndex;
      const cellOffset = this.state.wordLength * row;
      const guess = this.state.guess
      const greens = new Set()
      const freqDict = (string =>
        string.split('').reduce((freqDict, char) => {
          freqDict[char] = (freqDict[char] || 0) + 1;
          return freqDict;
        }, {}))(guess)
      console.log(freqDict)
      const corrects = new Set();
      const partials = new Set();
      for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
        const char = word[cInd]
        if (guess[cInd] === char) {
          corrects.add(cInd)
          freqDict[char] -= 1;
        }
      }
      for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
        const char = word[cInd]
        if (guess.indexOf(char) !== -1 && freqDict[char] > 0) {
          partials.add(cInd)
          freqDict[char] -= 1;
        }
      }
      for (let cInd = 0; cInd < this.state.wordLength; cInd++) {
        const char = word[cInd]
        this.keyboard.removeButtonTheme(char, 'partial miss correct')
        if (corrects.has(cInd)) {
          cells[cellOffset + cInd].classList.add('correct')
          this.keyboard.addButtonTheme(word[cInd], 'correct')
          greens.add(char)
        } else if (partials.has(cInd)) {
          cells[cellOffset + cInd].classList.add('partial')
          this.keyboard.addButtonTheme(word[cInd], 'partial')
        } else {
          cells[cellOffset + cInd].classList.add('miss')
          this.keyboard.addButtonTheme(word[cInd], 'miss')
        }
      }

      if (this.state.guess === word) {
        this.setState({
          over: true,
          won: true
        })
        window.localStorage.setItem(Constants.STORAGE.WIN, Number(window.localStorage.getItem(Constants.STORAGE.WIN) || 0) + 1)
      } else if (this.state.rowIndex < this.state.wordLength) {
        this.setState({
          rowIndex: this.state.rowIndex + 1,
          colIndex: 0,
        })
      } else {
        console.log('He lost')
        // toast.warn('You Lost', {
        //   position: 'top-center',
        //   autoClose: 3000
        // })
        this.setState({
          over: true,
          lost: true
        })
        window.localStorage.setItem(Constants.STORAGE.LOST, Number(window.localStorage.getItem(Constants.STORAGE.LOST) || 0) + 1)
      }

    } else {
      console.log('not a dictionary word')
      toast.warn('Not a known word', {
        position: 'top-center',
        autoClose: 3000
      })
    }
  }

  onKeyPress = (button) => {
    console.log("Button pressed", button);
    if (this.state.over && button !== '{enter}') return;
    if (button === '{bksp}') {
      if (this.state.colIndex > 0) {
        const tmp = this.state.inputs
        tmp[this.state.rowIndex][this.state.colIndex - 1] = ''
        this.setState({
          inputs: tmp,
          colIndex: this.state.colIndex - 1
        })
      }
    } else if (button === '{enter}') {
      if (this.state.over) {
        this.restart()
      } else {
        this.checkWord()
      }
    } else {
      if (this.state.rowIndex <= this.state.wordLength && this.state.colIndex < this.state.wordLength) {
        const tmp = this.state.inputs
        tmp[this.state.rowIndex][this.state.colIndex] = button
        this.setState({
          inputs: tmp,
          colIndex: this.state.colIndex + 1
        })
      }
    }
  }

  get renderResultBlocks() {
    let result = ''
    const guess = this.state.guess;
    for (let i = 0; i < this.state.inputs.length; i++) {
      const word = this.state.inputs[i].join('')
      if (!word.length) break;
      for (let j = 0; j < word.length; j++) {
        if (word[j] === guess[j]) {
          result += ''
        } else if (guess.indexOf(word[j]) !== -1) {
          result += ''
        } else {
          result += ''
        }
      }
      result += '\n'
    }
    return (
      <button className='share-btn' onClick={() => {
        const title = "Wordle Unlimited"
        const message = `*Wordle Unlimited*\n\nI have guessed the word in Wordle Unlimited.\n${result}\n\nDo you want to try?\n`
        const url = "https://opensourcefeed.org/puzzles/wordle-unlimited/";
        if (window.navigator.share) {
          window.navigator.share({
            text: message,
            url: url,
            title: title,
          })
        } else {
          navigator.clipboard.writeText(`${message}${url}`)
          toast('Content has been copied to clipboard. You can paste and share now')
        }
      }}>{''}Share</button>)
  }

  get renderCells() {
    console.log(this, '-------------')
    const offsetWidth = (320) - this.state.wordLength * 20;
    const indices = []
    for (let i = 0; i <= this.state.wordLength; i++) {
      indices.push(i)
    }
    const rows = this.state.inputs.length ? indices.map(index => <tr>{indices.slice(0, this.state.wordLength).map(cInd => <td>
      <div className='cell' defaultValue={''} style={{
        height: offsetWidth / this.state.wordLength,
        width: offsetWidth / this.state.wordLength,
        fontSize: offsetWidth / this.state.wordLength - 8
      }}>{this.state.inputs[index][cInd]}</div></td>)}</tr>) : null
    return <table className='table input-cells'><tbody>
      {rows}
    </tbody>
    </table>
  }

  get randomColor() {
    const colors = ['red', 'yellow', 'white', 'orange']
    return colors[Math.floor(Math.random() * colors.length)]
  }

  get renderHeader() {
    return (<header>
      <table className='header-table'>
        <tbody><tr>
          <td><span className="icon" onClick={this.toggleThemeMode.bind(this)}>
            {this.state.themeMode === 'dark' && <svg xmlns="http://www.w3.org/2000/svg" enable-background="new 0 0 24 24" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><rect fill="none" height="24" width="24"/><path d="M12,7c-2.76,0-5,2.24-5,5s2.24,5,5,5s5-2.24,5-5S14.76,7,12,7L12,7z M2,13l2,0c0.55,0,1-0.45,1-1s-0.45-1-1-1l-2,0 c-0.55,0-1,0.45-1,1S1.45,13,2,13z M20,13l2,0c0.55,0,1-0.45,1-1s-0.45-1-1-1l-2,0c-0.55,0-1,0.45-1,1S19.45,13,20,13z M11,2v2 c0,0.55,0.45,1,1,1s1-0.45,1-1V2c0-0.55-0.45-1-1-1S11,1.45,11,2z M11,20v2c0,0.55,0.45,1,1,1s1-0.45,1-1v-2c0-0.55-0.45-1-1-1 C11.45,19,11,19.45,11,20z M5.99,4.58c-0.39-0.39-1.03-0.39-1.41,0c-0.39,0.39-0.39,1.03,0,1.41l1.06,1.06 c0.39,0.39,1.03,0.39,1.41,0s0.39-1.03,0-1.41L5.99,4.58z M18.36,16.95c-0.39-0.39-1.03-0.39-1.41,0c-0.39,0.39-0.39,1.03,0,1.41 l1.06,1.06c0.39,0.39,1.03,0.39,1.41,0c0.39-0.39,0.39-1.03,0-1.41L18.36,16.95z M19.42,5.99c0.39-0.39,0.39-1.03,0-1.41 c-0.39-0.39-1.03-0.39-1.41,0l-1.06,1.06c-0.39,0.39-0.39,1.03,0,1.41s1.03,0.39,1.41,0L19.42,5.99z M7.05,18.36 c0.39-0.39,0.39-1.03,0-1.41c-0.39-0.39-1.03-0.39-1.41,0l-1.06,1.06c-0.39,0.39-0.39,1.03,0,1.41s1.03,0.39,1.41,0L7.05,18.36z"/></svg>}
            {this.state.themeMode === 'light' && <svg xmlns="http://www.w3.org/2000/svg" enable-background="new 0 0 24 24" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><rect fill="none" height="24" width="24"/><path d="M11.01,3.05C6.51,3.54,3,7.36,3,12c0,4.97,4.03,9,9,9c4.63,0,8.45-3.5,8.95-8c0.09-0.79-0.78-1.42-1.54-0.95 c-0.84,0.54-1.84,0.85-2.91,0.85c-2.98,0-5.4-2.42-5.4-5.4c0-1.06,0.31-2.06,0.84-2.89C12.39,3.94,11.9,2.98,11.01,3.05z"/></svg>}
          </span></td>
          <td><span className='icon' style={{
            visibility: 'hidden'
          }}>
          <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zM8 17c-.55 0-1-.45-1-1v-5c0-.55.45-1 1-1s1 .45 1 1v5c0 .55-.45 1-1 1zm4 0c-.55 0-1-.45-1-1V8c0-.55.45-1 1-1s1 .45 1 1v8c0 .55-.45 1-1 1zm4 0c-.55 0-1-.45-1-1v-2c0-.55.45-1 1-1s1 .45 1 1v2c0 .55-.45 1-1 1z" /></svg>
          </span></td>
          <td className='title-wrapper'><h1 className="title">Wordle Unlimited</h1></td>
          <td><span className='icon' onClick={() => {
            this.setState({
              showStats: true
            })
          }}>
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zM8 17c-.55 0-1-.45-1-1v-5c0-.55.45-1 1-1s1 .45 1 1v5c0 .55-.45 1-1 1zm4 0c-.55 0-1-.45-1-1V8c0-.55.45-1 1-1s1 .45 1 1v8c0 .55-.45 1-1 1zm4 0c-.55 0-1-.45-1-1v-2c0-.55.45-1 1-1s1 .45 1 1v2c0 .55-.45 1-1 1z" /></svg>
          </span></td>
          <td style={{ textAlign: 'center' }}><span onClick={() => {
            this.setState({
              showHelp: true
            })
          }} className='icon'>
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8zm-1-4h2v2h-2zm1.61-9.96c-2.06-.3-3.88.97-4.43 2.79-.18.58.26 1.17.87 1.17h.2c.41 0 .74-.29.88-.67.32-.89 1.27-1.5 2.3-1.28.95.2 1.65 1.13 1.57 2.1-.1 1.34-1.62 1.63-2.45 2.88 0 .01-.01.01-.01.02-.01.02-.02.03-.03.05-.09.15-.18.32-.25.5-.01.03-.03.05-.04.08-.01.02-.01.04-.02.07-.12.34-.2.75-.2 1.25h2c0-.42.11-.77.28-1.07.02-.03.03-.06.05-.09.08-.14.18-.27.28-.39.01-.01.02-.03.03-.04.1-.12.21-.23.33-.34.96-.91 2.26-1.65 1.99-3.56-.24-1.74-1.61-3.21-3.35-3.47z"/></svg>
            </span></td>
        </tr>
        </tbody>
      </table>
    </header>)
  }

  render() {
    return (
      <div className="App" ref={this.myContainer}>
        {this.renderHeader}
        {/* I have read {this.state.words.length} words, so far!!.
        {this.state.guess && <div>I guess the word {this.state.guess}</div>} */}
        {this.renderCells}
        {this.state.won && <div className='message message-success'>
          <strong>Brilliant </strong><br />
          You have found the word<br />
          {this.renderResultBlocks}<br />
          <p className='underline'>Press Enter for another Game</p>
        </div>
        }
        {this.state.lost && <div className='message message-failure'><strong>You Lost! </strong><br /> The word was <strong>{this.state.guess}</strong>. <br />
          <p className='underline'>Press Enter for another Game</p>
        </div>}
        <div className='keys' style={{ visibility: this.state.showHelp ? 'hidden' : 'visible' }}>
          <Keyboard
            keyboardRef={r => this.keyboard = r}
            onKeyPress={this.onKeyPress.bind(this)}
            layoutName='default'
            layout={{
              'default': [
                'Q W E R T Y U I O P',
                'A S D F G H J K L',
                '{enter} Z X C V B N M {bksp}',
              ]
            }}
            display={{
              '{enter}': 'ENTER',
              '{bksp}': '&lArr;'
            }}
            physicalKeyboardHighlight={true}
            physicalKeyboardHighlightPress={true}
          />
        </div>
        <ToastContainer />
        {this.state.won && <Snowfall color={this.randomColor} />}
        {this.state.showHelp && <Help secret={this.state.guess} onCloseRequest={() => {
          this.setState({
            showHelp: false
          })
        }} />}
        {this.state.showStats && <Stats secret={this.state.guess} onCloseRequest={() => {
          this.setState({
            showStats: false
          })
        }} />}
      </div>
    );
  }
}

export default App;
