import React, { Component } from 'react'
import Constants from './Constants'
import './Stats.scss'

export default class Stats extends Component {

  render() {
    const wins = Number(window.localStorage.getItem(Constants.STORAGE.WIN) || 0);
    const lost = Number(window.localStorage.getItem(Constants.STORAGE.LOST) || 0);
    return (
      <div className='modal'>
        <div className='content'>
          <h2>Stats</h2>
          <hr />
          <table className='result-table'>
            <tbody>
              <tr>
                <td>Total </td>
                <td>{wins + lost}</td>
              </tr>
              <tr>
                <td>Won </td>
                <td>{wins}</td>
              </tr>
              <tr>
                <td>Lost </td>
                <td>{lost}</td>
              </tr>
            </tbody>
          </table>
          <hr/>
          <div className='close' onClick={this.props.onCloseRequest}>CLOSE</div>
        </div>
      </div>
    )
  }
}